.container {
  padding-left: 20px;
  padding-right: 20px;
}

.footer {
  background-color: #fff5f6;
}

.footer__title-container {
  max-width: 234px;
  margin-bottom: 43px;

  @include tab {
    width: 340px;
  }
}

.footer__title {
  font-family: Titan One;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.17;
  text-transform: uppercase;
  color: #ffa5ba;
}

.footer__accent-title {
  display: block;

  font-size: 34px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  color: $brand-color;
}

.footer__contacts-container {
  margin-bottom: 43px;
}

.networks {
  display: flex;
  flex-direction: row;

  justify-content: flex-end;

  margin-bottom: 46px;
}

.networks__item:not(:last-child) {
  margin-right: 28px;
}

.networks__link {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 32px;
  height: 32px;
  background-color: #fea5bb;
  border-radius: 50%;
}

.networks__link:hover {
  background-color: $brand-color;
}

.networks__icon--github {
  width: 19px;
  height: 19px;
}

.networks__icon--telegram {
  width: 21px;
  height: 21px;
  transform: translateX(-1px);
}

.networks__icon--facebook {
  width: 20px;
  height: 20px;
}

.call {
  font-family: DM Sans;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.86;

  text-align: right;
}

.call__link {
  color: $brand-color;
  text-decoration: none;
}

.call__link:hover {
  color: #c7133e;
}

.copy-container {
  text-align: center;
  padding: 22px 0 25px 0;
  border-top: 1px solid #e1e1e1;
}

.сopyright {
  font-family: DM Sans;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;
  color: #907e82;

  @media screen and (max-width: 480px) {
    display: inline-block;
    max-width: 150px;
  }
}

@media screen and (max-width: 480px) {
  .copyright__accent {
    display: block;
  }
}
